import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-submitinfo',
  templateUrl: './submitinfo.component.html',
  styleUrls: ['./submitinfo.component.scss']
})
export class SubmitinfoComponent implements OnInit {

  usercontainer:object[]=[];

  adduser(userdata)
  {
    var user=
    {
      name:userdata.value.UserName,
      age:userdata.value.UserAge,
      mail:userdata.value.UserMail,
      phone:userdata.value.UserPhone

    }
    this.usercontainer.push(user);

    console.log(this.usercontainer);
  }
  constructor() { }

  ngOnInit() {
  }

}
